: 10px;\n  margin-left: auto;\n  margin-right: auto;\n  width: 100%;\n  max-width: 380px;\n  justify-content: space-around;\n  background-color: rgba(208, 210, 211, 0.041);\n  transition: transform 0.09s;\n  border-radius: 2%;\n\n  filter: grayscale(1);\n  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);\n\n  // box-shadow: 1px 1px 0px 0px #cecbcb;\n}\n\n.card:hover {\n  transform: scale(\n    1.01\n  ); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */\n}\n\n.serial {\n  font-display: swap;\n  font-family: \"Press Start 2P\", cursive;\n  // font-family: \"Open Sans\", Arial, sans-serif;\n  font-weight: 400;\n  font-size: 18pt;\n  max-width: 280px;\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n  flex-direction: column;\n  margin-left: auto;\n  margin-right: auto;\n  margin-top: 1rem;\n  display: flex;\n}\n\n.date {\n  font-display: swap;\n  color: #020202;\n  font-family: \"Quicksand\", sans-serif;\n  // font-family: \"Open Sans\", Arial, sans-serif;\n  font-size: 1.5rem;\n  max-width: 280px;\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n  flex-direction: column;\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n}\n\n.address {\n  color: #020202;\n  font-display: swap;\n  font-family: \"Quicksand\", sans-serif;\n  // font-family: \"Press Start 2P\", cursive;\n  // font-family: \"Press Start 2P\", cursive;\n  // font-family: \"Open Sans\", Arial, sans-serif;\n  font-size: 1.2rem;\n  max-width: 280px;\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n  flex-direction: column;\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n}\n\n.status {\n  color: #222121;\n\n  margin-bottom: 4pt;\n  margin-top: 1rem;\n\n  font-family: \"Press Start 2P\", cursive;\n  font-display: swap;\n\n  font-weight: bolder;\n  font-size: 1.4rem;\n  max-width: 280px;\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n  flex-direction: column;\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n}\n\n.deposit {\n  color: #020202;\n  font-family: \"Quicksand\", sans-serif;\n  font-display: swap;\n\n  font-size: 1.2rem;\n  max-width: 280px;\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n  flex-direction: column;\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n  margin-bottom: 1rem;\n}\n\n.style {\n  font-family: \"Quicksand\", sans-serif;\n  font-display: swap;\n  color: #020202;\n\n  font-size: 1.2rem;\n  max-width: 280px;\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n  flex-direction: column;\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 1rem;\n  display: flex;\n}\n\n.list-item {\n  justify-content: center;\n\n  display: grid;\n  padding: 0 10vw;\n\n  grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));\n\n  gap: 1px;\n}\n\n.icon-image img {\n  margin-left: auto;\n  margin-right: auto;\n  display: block;\n  margin-top: 1.5rem;\n  height: 6vh;\n}\n\n.main-header {\n  position: relative;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  height: 65px;\n  width: 100vw;\n  padding: 0 10vw;\n  color: #000;\n  box-shadow: 1.8px 1.8px 1.8px 1.8px rgba(212, 214, 212, 0.5);\n  z-index: 1;\n}\n\n.thumb {\n  border-radius: 3px;\n\n  width: auto;\n  height: 10vh;\n  position: relative;\n  display: flex;\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 1rem;\n  background-repeat: no-repeat;\n  background-position: center;\n  background-size: cover;\n}\n\n.logo {\n  color: #020202;\n  font-family: \"Ewert\", cursive;\n  font-display: swap;\n  font-size: 2rem;\n  max-width: 400px;\n\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n  margin-bottom: 1rem;\n  object-fit: cover;\n}\n\n.not-container {\n  background: url(\"https://i.postimg.cc/wTjbqjsv/20945153-Convert-Image.jpg\") no-repeat center\n    center;\n  text-align: center;\n  width: 100%;\n  height: 44vh;\n  margin-bottom: -1rem;\n  background-size: contain;\n  display: flex;\n}\n\n.octopus {\n  height: 30vh;\n  margin-bottom: 0%;\n  margin-top: 0px;\n}\n\nh3 {\n  font-family: \"Montserrat\", sans-serif;\n  font-display: swap;\n  font-size: 1.8em;\n  max-width: 1100px;\n  margin: 0 auto;\n  margin-top: -1.4rem;\n  margin-bottom: 1rem;\n  text-align: center;\n}\n\n@media (max-width: 26.25em) {\n  h3 {\n    font-size: 1.2em;\n  }\n}\n\nh1 {\n  font-family: \"Reenie Beanie\", cursive;\n  font-display: swap;\n  font-size: 6em;\n\n  margin: 0 auto;\n  margin-bottom: -1rem;\n  margin-top: 0.2em;\n  text-align: center;\n}\n\n@media (max-width: 26.25em) {\n  h1 {\n    font-size: 4em;\n  }\n}\n\n.not-map {\n  width: 100%;\n  height: 60vh;\n  padding: 10px;\n  margin-bottom: 2rem;\n  position: relative;\n}\n\n.map {\n  width: 100%;\n  // height: 60vh;\n  padding: 10px;\n  margin-bottom: 1rem;\n  position: relative;\n}\n\n.webgl {\n  position: absolute;\n  top: 0;\n  left: 0;\n  outline: none;\n  mix-blend-mode: exclusion;\n}\n\n.favorite-button {\n  width: auto;\n  height: 4vh;\n  margin-bottom: 10pt;\n}\n\n.button-container {\n  width: 100%;\n  height: 10vh;\n\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.button {\n  font-family: \"Ubuntu Mono\", monospace;\n  font-display: swap;\n  border-radius: 2rem;\n  font-weight: 600;\n  margin-right: 10pt;\n}\n\n.about-us-text {\n  font-display: swap;\n  font-family: \"Montserrat\", sans-serif;\n  font-size: 1em;\n  max-width: 1000px;\n  margin: 0 auto;\n  color: grey;\n  text-align: left;\n  margin-bottom: 10pt;\n  margin-top: 10pt;\n}\n\n.about-us-title {\n  font-display: swap;\n  font-family: \"Source Code Pro\", monospace;\n  font-size: 4em;\n  color: rgb(70, 69, 69);\n  margin: 0 auto;\n\n  text-align: center;\n}\n\n.sign-in {\n  margin-right: 10px;\n}\n\n.navbar-brand-text {\n  margin-right: 1rem;\n  margin-top: 5px;\n  text-align: center;\n  display: flex;\n  font-size: 1.4rem;\n  font-weight: 300;\n  color: #312f2f;\n}\n\n.d-inline-block {\n  margin-left: 1.4rem;\n  border-radius: 49%;\n  box-shadow: #383434;\n  width: 100;\n  height: 12vh;\n}\n","/**\n * Foundation for Sites\n * Version 6.6.3\n * https://get.foundation\n * Licensed under MIT Open Source\n */\n\n// --- Dependencies ---\n@import 'vendor/normalize';\n@import '../_vendor/sassy-lists/stylesheets/helpers/missing-dependencies';\n@import '../_vendor/sassy-lists/stylesheets/helpers/true';\n@import '../_vendor/sassy-lists/stylesheets/functions/contain';\n@import '../_vendor/sassy-lists/stylesheets/functions/purge';\n@import '../_vendor/sassy-lists/stylesheets/functions/remove';\n@import '../_vendor/sassy-lists/stylesheets/functions/replace';\n@import '../_vendor/sassy-lists/stylesheets/functions/to-list';\n\n// --- Settings ---\n// import your own `settings` here or\n// import and modify the default settings through\n// @import 'settings/settings';\n\n// --- Components ---\n// Utilities\n@import 'util/util';\n// Global styles\n@import 'global';\n@import 'forms/forms';\n@import 'typography/typography';\n\n// Grids\n@import 'grid/grid';\n@import 'xy-grid/xy-grid';\n// Generic components\n@import 'components/button';\n@import 'components/button-group';\n@import 'components/close-button';\n@import 'components/label';\n@import 'components/progress-bar';\n@import 'components/slider';\n@import 'components/switch';\n@import 'components/table';\n// Basic components\n@import 'components/badge';\n@import 'components/breadcrumbs';\n@import 'components/callout';\n@import 'components/card';\n@import 'components/dropdown';\n@import 'components/pagination';\n@import 'components/tooltip';\n\n// Containers\n@import 'components/accordion';\n@import 'components/media-object';\n@import 'components/orbit';\n@import 'components/responsive-embed';\n@import 'components/tabs';\n@import 'components/thumbnail';\n// Menu-based containers\n@import 'components/menu';\n@import 'components/menu-icon';\n@import 'components/accordion-menu';\n@import 'components/drilldown';\n@import 'components/dropdown-menu';\n\n// Layout components\n@import 'components/off-canvas';\n@import 'components/reveal';\n@import 'components/sticky';\n@import 'components/title-bar';\n@import 'components/top-bar';\n\n// Helpers\n@import 'components/float';\n@import 'components/flex';\n@import 'components/visibility';\n@import 'prototype/prototype';\n\n\n@mixin foundation-everything(\n  $flex: true,\n  $prototype: false,\n  $xy-grid: $xy-grid\n) {\n  @if $flex {\n    $global-flexbox: true !global;\n  }\n\n  @if $xy-grid {\n    $xy-grid: true !global;\n  }\n\n  // Global styles\n  @include foundation-global-styles;\n  @include foundation-forms;\n  @include foundation-typography;\n\n  // Grids\n  @if not $flex {\n    @include foundation-grid;\n  }\n  @else {\n    @if $xy-grid {\n      @include foundation-xy-grid-classes;\n    }\n    @else {\n      @include foundation-flex-grid;\n    }\n  }\n\n  // Generic components\n  @include foundation-button;\n  @include foundation-button-group;\n  @include foundation-close-button;\n  @include foundation-label;\n  @include foundation-progress-bar;\n  @include foundation-slider;\n  @include foundation-switch;\n  @include foundation-table;\n  // Basic components\n  @include foundation-badge;\n  @include foundation-breadcrumbs;\n  @include foundation-callout;\n  @include foundation-card;\n  @include foundation-dropdown;\n  @include foundation-pagination;\n  @include foundation-tooltip;\n\n  // Containers\n  @include foundation-accordion;\n  @include foundation-media-object;\n  @include foundation-orbit;\n  @include foundation-responsive-embed;\n  @include foundation-tabs;\n  @include foundation-thumbnail;\n  // Menu-based containers\n  @include foundation-menu;\n  @include foundation-menu-icon;\n  @include foundation-accordion-menu;\n  @include foundation-drilldown-menu;\n  @include foundation-dropdown-menu;\n\n  // Layout components\n  @include foundation-off-canvas;\n  @include foundation-reveal;\n  @include foundation-sticky;\n  @include foundation-title-bar;\n  @include foundation-top-bar;\n\n  // Helpers\n  @include foundation-float-classes;\n  @if $flex { @include foundation-flex-classes; }\n  @include foundation-visibility-classes;\n  @if $prototype { @include foundation-prototype-classes; }\n}\n","// Foundation for Sites\n// https://get.foundation\n// Licensed under MIT Open Source\n\n////\n/// @group breakpoints\n////\n\n/// A list of named breakpoints. You can use these with the `breakpoint()` mixin to quickly create media queries.\n/// @type Map\n$breakpoints: (\n  small: 0,\n  medium: 640px,\n  large: 1024px,\n  xlarge: 1200px,\n  xxlarge: 1440px,\n) !default;\n\n/// A list of named HiDPI breakpoints. You can use these with the `breakpoint()` mixin to quickly create media queries for resolutions.\n/// Values must represent the device pixels / web pixels ration and be unitless or in DPPX.\n/// @type Map\n$breakpoints-hidpi: (\n  hidpi-1: 1,\n  hidpi-1-5: 1.5,\n  hidpi-2: 2,\n  retina: 2,\n  hidpi-3: 3\n) !default;\n\n/// The largest named breakpoint in which to include print as a media type\n/// @type Keyword\n$print-breakpoint: large !default;\n\n$-zf-zero-breakpoint: small !default;\n\n$-zf-breakpoints-keys: map-to-list($breakpoints, 'keys');\n\n@if nth(map-values($breakpoints), 1) != 0 {\n  @error 'The first key in the $breakpoints map must have a value of \"0\".';\n}\n@else {\n  $-zf-zero-breakpoint: nth(map-keys($breakpoints), 1);\n}\n\n/// All of the names in this list will be output as classes in your CSS, like `.small-12`, `.medium-6`, and so on. Each value in this list must also be in the `$breakpoints` map.\n/// @type List\n$breakpoint-classes: (small medium large) !default;\n\n/// Generates a media query string matching the input value. Refer to the documentation for the `breakpoint()` mixin to see what the possible inputs are.\n///\n/// @param {Keyword|Number} $val [small] - Breakpoint name, or px, rem, or em value to process.\n@function breakpoint($val: $-zf-zero-breakpoint) {\n  // Web standard Pixels per inch. (1ddpx / $std-web-dpi) = 1dpi\n  // See https://www.w3.org/TR/css-values-3/#absolute-lengths\n  $std-web-dpi: 96;\n\n  // Size or keyword\n  $bp: nth($val, 1);\n  // Value of the following breakpoint\n  $bp-next: null;\n  // Value for max-width media queries\n  $bp-min: null;\n  // Value for min-width media queries\n  $bp-max: null;\n  // Direction of media query (up, down, or only)\n  $dir: if(length($val) > 1, nth($val, 2), up);\n  // If named, name of the breakpoint\n  $name: null;\n  // If the breakpoint is a HiDPI breakpoint\n  $hidpi: false;\n\n  // Orientation media queries have a unique syntax\n  @if $bp == 'landscape' or $bp == 'portrait' {\n    @return '(orientation: #{$bp})';\n  }\n\n  // If a breakpoint name is given, get its value from the $breakpoints/$breakpoints-hidpi map.\n  @if type-of($bp) == 'string' {\n    @if map-has-key($breakpoints, $bp) {\n      $name: $bp;\n      $bp: map-get($breakpoints, $name);\n      $bp-next: -zf-map-next($breakpoints, $name);\n    }\n    @else if map-has-key($breakpoints-hidpi, $bp) {\n      $name: $bp;\n      $bp: map-get($breakpoints-hidpi, $name);\n      $bp-next: -zf-map-next-number($breakpoints-hidpi, $bp);\n      $hidpi: true;\n    }\n    @else {\n      $bp: 0;\n      @warn 'breakpoint(): \"#{$val}\" is not defined in your `$breakpoints` or `$breakpoints-hidpi` setting.';\n    }\n  }\n\n  @if not $name and $dir == 'only' {\n    @warn 'breakpoint(): Only named media queries can have an `only` range.';\n    @return null;\n  }\n\n  // Only 'only' and 'up' have a min limit.\n  @if $dir == 'only' or $dir == 'up' {\n    $bp-min: if($hidpi, strip-unit($bp), -zf-bp-to-em($bp));\n  }\n  // Only 'only' and 'down' have a max limit.\n  @if $dir == 'only' or $dir == 'down' {\n    // If the breakpoint is a value, use it as max limit.\n    @if not $name {\n      $bp-max: if($hidpi, strip-unit($bp), -zf-bp-to-em($bp));\n    }\n    // If the breakpoint is named, the max limit